.headline-h2 {
  h2 {
    color: #45504d;
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
  }
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #f8f8f8;
  z-index: 999;

  ul {
    display: flex;
    height: 44px;
    li {
      flex: 1;
      text-align: center;
      padding-top: 2px;

      a {
        color: #444444;
      }

      i,
      span {
        display: block;
      }

      i {
        font-size: 18px;
        line-height: 22px;
      }

      span {
        font-size: 12px;
        line-height: 18px;
      }

      &.on{
        a{
          color: #92b1c1!important;
          &:hover{
            
          }
        }
      }
    }
  }
}
